import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs';
import { BehaviorSubject } from 'rxjs';
import {
  map, filter, catchError, mergeMap,
} from 'rxjs/operators';
import { Gst } from './gst';
@Injectable({
  providedIn: 'root'
})
export class GstService {
  private baseUrl: string = 'http://localhost:8080/api';

  private headers = new Headers({ 'Content-Type': 'application/json' });

  private options = new RequestOptions({ headers: this.headers });
  dialogData: any;

  dataChange: BehaviorSubject<Gst[]> = new BehaviorSubject<Gst[]>([]);

  private gst: Gst;
  constructor(private _http: Http) { }
  get data(): Gst[] {
    return this.dataChange.value;
  }
  getGst() {
    return this._http.get(this.baseUrl + '/getGst', this.options)
      .pipe(map((response: Response) => response.json()),
        catchError((error) => {
          return Observable.throw(error);
        }));
  }
  saveGst(gst: Gst) {
    return this._http.post(this.baseUrl + '/saveGst', JSON.stringify(gst), this.options)
      .pipe(map((response: Response) => response.json()),
        catchError((error) => {
          return Observable.throw(error);
        }));
  }
  updateGst(gst: Gst) {
    return this._http.put(this.baseUrl + '/updateGst', JSON.stringify(gst), this.options)
      .pipe(map((response: Response) => response.json()),
        catchError((error) => {
          return Observable.throw(error);
        }));
  }
  deleteGst(gstId: number) {
    return this._http.delete(this.baseUrl + '/deleteGst/' + gstId, this.options)
      .pipe(map((response: Response) => response.json()),
        catchError((error) => {
          return Observable.throw(error);
        }));
  }
  selectGst(gstId:number){
    return this._http.get(this.baseUrl+'/selectGst/'+gstId,this.options)
    .pipe(map((response: Response) => response.json()),
        catchError((error) => {
          return Observable.throw(error);
        }));
  }
  getDialogData() {
    return this.dialogData;
  }

  setter(gst: Gst) {
    this.gst = gst;
  }

  getter() {
    return this.gst;
  }
}
