import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { map, catchError } from 'rxjs/operators';
import { Observable } from 'rxjs';
import { user } from '../restaurants/admin/settings/accesscontrol/user';
import { Role } from '../restaurants/admin/settings/accesscontrol/role';
@Injectable({
  providedIn: 'root'
})
export class LoginService {

  private baseUrl: string = 'http://localhost:8080/api';

  private headers = new Headers({ 'Content-Type': 'application/json' });

  private options = new RequestOptions({ headers: this.headers });
  private user: user;
  private role: Role[];
  private name;
  private hours = 24; // Reset when storage is more than 24hours
  private now = new Date().getTime();
  private setupTime = localStorage.getItem('currentUser');
  constructor(public _http: Http) { }
  public logIn(user: user) {
    let headers = new Headers();
    headers.append('Accept', 'application/json')
    // creating base64 encoded String from user name and password
    var base64Credential: string = btoa(user.userName + ':' + user.password);
    headers.append("Authorization", "Basic " + base64Credential);
    let options = new RequestOptions();
    options.headers = headers;
    return this._http.get('http://localhost:8080/api/login', options)
      .pipe(map((response: Response) => {
        // login successful if there's a jwt token in the response
        let user = response.json().principal;// the returned user object is a principal object
        if (user) {
          //store user details  in local storage to keep user logged in between page refreshes
          this.role = user.role;
          this.name = user.userName;
          console.log(user.password);
          console.log('Role:', JSON.stringify(this.role[0].role));
          //if(this.setupTime = null){
          localStorage.setItem('currentUser', JSON.stringify(user));
          localStorage.setItem('userRole', JSON.stringify(this.role[0].role));
          localStorage.setItem('timeOut', this.now.toString());
          // }
          //  else{
          //  if(this.now > this.hours*60*60*1000) {
          // //     localStorage.clear();
          // }
          //  }

        }
        return user;
      }));
  }

  getUserId() {
    return this._http.get(this.baseUrl + '/getUserId', this.options)
      .pipe(map((response: Response) => {
        let id=response.json();
        localStorage.setItem('userId', id);
      }),
        catchError((error: any) => {
          return Observable.throw(error);
        }))
  }
  roleMatch(allowedRoles): boolean {
    var isMatch = false;
    var userRoles: string[] = JSON.parse(localStorage.getItem('userRole'));
    allowedRoles.forEach(element => {
      if (userRoles.indexOf(element) > -1) {
        isMatch = true;
        return false;
      }
    });
    return isMatch;
  }
}
