import { Injectable } from '@angular/core';
import {RequestOptions,Headers,Http,Response} from '@angular/http';
import {HttpClient} from '@angular/common/http';
import {ItemGroupMaster} from './item-group-master';
import {Observable,BehaviorSubject} from 'rxjs';
import {map,catchError} from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})


export class ItemGroupMasterService {
  private baseUrl:string ='http://localhost:8080/api';
  private headers = new Headers({ 'Content-Type': 'application/json' });
  private options = new RequestOptions({ headers: this.headers });
  private itemGroupMaster: ItemGroupMaster;
  dialogData: any;
  dataChange: BehaviorSubject<ItemGroupMaster[]> = new BehaviorSubject<ItemGroupMaster[]>([]);
  constructor(private _http: Http) { }

  getAllItemGroup(): Observable<ItemGroupMaster[]> {
    return this._http.get(this.baseUrl + '/getAllItemGroup', this.options)
      .pipe(map((response: Response) => response.json()),
        catchError((error: any) => {
          return Observable.throw(error);
        }));
  }
  getter(){
    return  this.itemGroupMaster;
  }

  setter(itemGroupMaster: ItemGroupMaster) {
    this.itemGroupMaster = itemGroupMaster;
  }
 

  saveItemGroupMaster(itemGroupMaster:ItemGroupMaster):Observable<any>{
    return this._http.post(this.baseUrl +'/saveItemGroup', JSON.stringify(itemGroupMaster),this.options)  
    .pipe(map((response:Response)=> response.json()),
     catchError((error:any) => {
    return Observable.throw(error);
}))
}

deleteItemGroupMaster(id: Number): Observable<any> {
  return this._http.delete(this.baseUrl + '/deleteItemGroup/' + id, this.options)
    .pipe(map((response: Response) => response.json()),
      catchError((error: any) => {
        return Observable.throw(error);
      }))

}
editItemGroupMaster(itemGroupMaster:ItemGroupMaster): Observable<any>{
  return this._http.put(this.baseUrl + '/updateItemGroup' ,JSON.stringify(itemGroupMaster), this.options)
  .pipe(map((response: Response) => response.json()),
  catchError((error: any) => {
    return Observable.throw(error);
  }));

}



}
