import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs';
import { BehaviorSubject } from 'rxjs';
import {
  map, filter, catchError, mergeMap,
} from 'rxjs/operators';
import { MenuGroupMaster } from './menu-group-master';
@Injectable({
  providedIn: 'root'
})
export class MenuGroupMasterService {
  private baseUrl: string = 'http://localhost:8080/api';

  private headers = new Headers({ 'Content-Type': 'application/json' });

  private options = new RequestOptions({ headers: this.headers });
  dialogData: any;

  dataChange: BehaviorSubject<MenuGroupMaster[]> = new BehaviorSubject<MenuGroupMaster[]>([]);

  private menuGroup: MenuGroupMaster;

  constructor(private _http: Http) { }

  get data(): MenuGroupMaster[] {
    return this.dataChange.value;
  }

  

  getMenuGroup() {
    return this._http.get(this.baseUrl + '/getMenuGroup', this.options)
      .pipe(map((response: Response) => response.json()),
        catchError((error: any) => {
          return Observable.throw(error);
        }));
  }
  saveMenuGroup(menugroup: MenuGroupMaster) {
    return this._http.post(this.baseUrl + '/saveMenuGroup', JSON.stringify(menugroup), this.options)
      .pipe(map((response: Response) => response.json()),
        catchError((error: any) => {
          return Observable.throw(error);
        }));
  }
  updateMenuGroup(menugroup: MenuGroupMaster) {
    console.log(menugroup);
    return this._http.put(this.baseUrl + '/updateMenuGroup', JSON.stringify(menugroup), this.options)
      .pipe(map((response: Response) => response.json()),
        catchError((error: any) => {
          return Observable.throw(error);
        }));
  }
  deleteMenuGroup(menuGroupId: number) {
    console.log('s',menuGroupId);
    return this._http.delete(this.baseUrl + '/deleteMenuGroup/' + menuGroupId, this.options)
      .pipe(map((response: Response) => response.json()),
        catchError((error: any) => {
          return Observable.throw(error);
        }));
  }

  selectMenuCategory(menuGroupId:number){
    return this._http.get(this.baseUrl + '/selectMenuCategory/' + menuGroupId, this.options)
      .pipe(map((response: Response) => response.json()),
        catchError((error: any) => {
          return Observable.throw(error);
        }));
  }
  getDialogData() {
    return this.dialogData;
  }

  setter(menuGroup: MenuGroupMaster) {
    this.menuGroup = menuGroup;
  }

  getter() {
    return this.menuGroup;
  }

}
